
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.card {
  display: flex;
  flex-direction: column;

  padding: 15px 14px;
  width: 200px;

  border: 1px solid #f3f3f3;
  border-radius: 5px;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 15px auto 45px;
}

.card__name {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
}

.card__code {
  font-size: 11px;
  margin-top: 5px;
}

.card__rating {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 15px;
}

.card__stars {
  display: flex;
}

.stars__star {
  width: 15px;
  height: 15px;

  background: url("images/star.svg") no-repeat center;
}

.stars__star:nth-child(1n+2) {
  margin-left: 4px;
}

.stars--1 .stars__star:nth-child(-1n+1),
.stars--2 .stars__star:nth-child(-1n+2),
.stars--3 .stars__star:nth-child(-1n+3),
.stars--4 .stars__star:nth-child(-1n+4),
.stars--5 .stars__star:nth-child(-1n+5) {
  background: url("images/star-active.svg") no-repeat center;
}

.card__feedbacks {
  font-size: 11px;
}

.card__price {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 20px;
}

.card__prop {
  font-size: 11px;
  line-height: 13px;

  /*margin-bottom: 1px;*/
}

.card__value {
  font-weight: bold;
  font-size: 16px;
}

.card__btn {
  display: block;
  height: 40px;
  margin-top: 20px;

  font-size: 14px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  line-height: 40px;

  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card__btn:hover {
  color: #00acdc;
  background: #fff;
}
